<template>
  <v-row justify="center">
    <v-dialog v-model="visibility" persistent max-width="400">
      <v-card flat class="pa-2">
        <v-card-text>
          <v-icon large color="error">mdi-trash-can</v-icon>
          <h4>{{ message }}</h4>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="#09b" text @click="$emit('update:visibility', false)">Cancel</v-btn>
          <v-btn color="#900" text @click="confirm">Delete</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
export default {
  name: 'ConfirmDelete',
  props: {
    visibility: Boolean,
    message: {
      type: String,
      default: 'Are you sure you want to perform the delete operation?'
    },
    response: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    confirm () {
      this.$emit('update:response', true)
      this.$emit('update:visibility', false)
    }
  }
}
</script>
